import { ELocales } from '@aca-new/app/shared/models/enums/locales.enum';
import { HttpBackend, HttpClient } from '@angular/common/http';
import { TranslateService } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

/**
 * @description
 *  Use {@link HttpBackend} instead of {@link HttpClient} to avoid circular dependency due to interceptors catching the requests
 * @param {HttpBackend} httpBackend The http backend instance
 * @returns {TranslateHttpLoader} The loader
 */
export function HttpLoaderFactory(httpBackend: Readonly<HttpBackend>): TranslateHttpLoader {
  return new TranslateHttpLoader(new HttpClient(httpBackend));
}

/**
 * @description
 * Factory that will initialize the locale
 * @param {TranslateService} translate The locale service to initialize
 * @returns {() => Promise<void>} Function that initialize the locale service
 */
// eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
export function LocalInitProvider(translate: Readonly<TranslateService>): () => Promise<void> {
  return async (): Promise<void> => {
    translate.addLangs([ELocales.ENGLISH]);
    translate.setDefaultLang(ELocales.ENGLISH);
    await translate.use(ELocales.ENGLISH).toPromise();
  };
}
