import { EHttpStatusCode } from '@aca-new/app/shared/models/enums/http-status-code.enum';
import { HttpInterceptor, HttpRequest, HttpErrorResponse, HttpHandler, HttpEvent } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';

@Injectable()
export class ErrorHandlerInterceptor implements HttpInterceptor {
  // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
  public intercept<T>(request: Readonly<HttpRequest<T>>, next: Readonly<HttpHandler>): Observable<HttpEvent<T>> {
    return next.handle(request).pipe(
      tap({
        error(error: HttpErrorResponse | Error | unknown): void | never {
          if (error instanceof HttpErrorResponse) {
            if (error.status === EHttpStatusCode.FORBIDDEN) {
              // TODO handle 403 error
              return;
            }

            if (error.status === EHttpStatusCode.INTERNAL_SERVER_ERROR) {
              // TODO handle 500 error
              return;
            }

            // Rethrow since not handled
            throw error;
          } else if (error instanceof Error) {
            // Rethrow since not handled
            throw error;
          }

          // Rethrow since not handled
          throw new Error('Unknown error thrown');
        },
      })
    );
  }
}
