import { BookingsComponent } from '@aca-new/app/pages/bookings/bookings.component';
import { CompletedBookingsComponent } from '@aca-new/app/pages/bookings/pages/completed-bookings/completed-bookings.component';
import { DraftsBookingsComponent } from '@aca-new/app/pages/bookings/pages/drafts-bookings/drafts-bookings.component';
import { OpenBookingsComponent } from '@aca-new/app/pages/bookings/pages/open-bookings/open-bookings.component';
import { DataTableModule } from '@aca-new/app/shared/components/data-table/data-table.module';
import { AuthoritiesGuard } from '@aca-new/app/shared/guards/authorities/authorities.guard';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { QimaTabModule, QimaTabsAutoSelectDirectiveModule, QimaTabsModule } from '@qima/ngx-qima';

const ROUTES: Routes = [
  {
    path: '',
    component: BookingsComponent,
    canActivate: [AuthoritiesGuard],
    children: [
      {
        path: '',
        redirectTo: 'open',
        pathMatch: 'full',
      },
      {
        path: 'open',
        component: OpenBookingsComponent,
      },
      {
        path: 'completed',
        component: CompletedBookingsComponent,
      },
      {
        path: 'drafts',
        component: DraftsBookingsComponent,
      },
    ],
  },
];

@NgModule({
  declarations: [BookingsComponent, OpenBookingsComponent, CompletedBookingsComponent, DraftsBookingsComponent],
  imports: [
    RouterModule.forChild(ROUTES),
    TranslateModule,
    QimaTabsModule,
    QimaTabModule,
    QimaTabsAutoSelectDirectiveModule,
    CommonModule,
    DataTableModule,
  ],
  exports: [BookingsComponent, OpenBookingsComponent, CompletedBookingsComponent, DraftsBookingsComponent],
})
export class BookingsModule {}
