import { IUser } from '@aca-new/app/pages/user/shared/models/interfaces/user.interface';
import { AuthenticationService } from '@aca-new/app/shared/services/authentication.service';
import { DOCUMENT } from '@angular/common';
import { AfterViewInit, ChangeDetectionStrategy, Component, Inject } from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';

@UntilDestroy()
@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class MainComponent implements AfterViewInit {
  public constructor(
    private readonly _authenticationService: AuthenticationService,
    @Inject(DOCUMENT) private readonly _document: Document
  ) {}

  // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
  public initChat(userData: Readonly<IUser>): void {
    // TODO need to move to another position,such as router guard
    const [email] = userData.email?.split(';');
    const { login, firstName, lastName, id } = userData;
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const windowRef = this._document.defaultView as any;

    windowRef.MessageBirdChatWidget?.init();
    windowRef.MessageBirdChatWidget?.identify(id, {
      email,
      firstName,
      lastName,
      // eslint-disable-next-line @typescript-eslint/naming-convention
      Login: login,
    });
  }

  public ngAfterViewInit(): void {
    const userData: IUser = this._authenticationService.getCurrentUser();

    if (this._authenticationService.isAuthenticated()) {
      this.initChat(userData);
    }

    const { currentUserSubject$ } = this._authenticationService;

    // eslint-disable-next-line rxjs/no-ignored-subscription,@typescript-eslint/prefer-readonly-parameter-types
    currentUserSubject$.pipe(untilDestroyed(this)).subscribe((data: Readonly<IUser>): void => {
      // TODO It needs to be guaranteed to be executed only once
      this.initChat(data);
    });
  }
}
