import { IUser } from '@aca-new/app/pages/user/shared/models/interfaces/user.interface';
import { SERVER_API_URL } from '@aca-new/app/shared/constants/app.constants';
import { IHttpResponseBody } from '@aca-new/app/shared/models/interfaces/http-response-body.interface';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable()
export class UserService {
  public constructor(private readonly _httpClient: HttpClient) {}

  public getUserById$(userId: string): Observable<IUser | null> {
    return (
      this._httpClient
        .get<IHttpResponseBody<IUser>>(`${SERVER_API_URL}/user/v2/${userId}`, { observe: 'response' })
        // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
        .pipe(map((response: Readonly<HttpResponse<IHttpResponseBody<IUser>>>): IUser | null => response?.body?.content || null))
    );
  }
}
