import { ACCOUNT_SUMMARY } from '@aca-new/app/pages/landing/pages/account-summary/shared/constants/account-summary.constants';
import {
  AccountSummaryCurrencyData,
  AccountSummaryData,
  AccountSummaryPercentageData,
} from '@aca-new/app/pages/landing/pages/account-summary/shared/models/classes/account-summary-data.class';
import { AccountSummaryService } from '@aca-new/app/pages/landing/pages/account-summary/shared/service/account-summary.service';
import { IAccountSummaryCard } from '@aca-new/app/pages/landing/pages/account-summary/shared/service/models/interfaces/account-summary-card.interface';
import { IAccountSummary } from '@aca-new/app/pages/landing/pages/account-summary/shared/service/models/interfaces/account-summary.interface';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Inject, LOCALE_ID, OnInit } from '@angular/core';
import { untilDestroyed, UntilDestroy } from '@ngneat/until-destroy';
import { EQimaButtonSize, EQimaButtonStyle, EQimaDividerOrientation, QimaOptionalType } from '@qima/ngx-qima';

@UntilDestroy()
@Component({
  selector: 'app-account-summary',
  templateUrl: './account-summary.component.html',
  styleUrls: ['./account-summary.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [AccountSummaryService],
})
export class AccountSummaryComponent implements OnInit {
  public horizontal: EQimaDividerOrientation = EQimaDividerOrientation.HORIZONTAL;
  public bookingButtonSize: EQimaButtonSize = EQimaButtonSize.SMALL;
  public bookingButtonStyle: EQimaButtonStyle = EQimaButtonStyle.SECONDARY;
  public hasAccountSummaryContent: boolean = false;
  public accountSummary: IAccountSummary = {
    inspectionOrdersCompleted: 0,
    inspectionSupplierVisited: 0,
    inspectionAvgCost: 0,
    inspectionBeyondAQLRate: 0,
    inspectionServiceLevel: 0,
    inspectionRejectionRate: 0,
  };

  public inspectionOrdersCompleted: QimaOptionalType<IAccountSummaryCard> = undefined;
  public inspectionSupplierVisited: QimaOptionalType<IAccountSummaryCard> = undefined;
  public inspectionAvgCost: QimaOptionalType<IAccountSummaryCard> = undefined;
  public inspectionBeyondAQLRate: QimaOptionalType<IAccountSummaryCard> = undefined;
  public inspectionServiceLevel: QimaOptionalType<IAccountSummaryCard> = undefined;
  public inspectionRejectionRate: QimaOptionalType<IAccountSummaryCard> = undefined;

  public constructor(
    private readonly _accountSummaryService: AccountSummaryService,
    private readonly _changeDetectorRef: ChangeDetectorRef,
    @Inject(LOCALE_ID) public locale: string
  ) {}

  public getAccountSummaryStatus(accountSummary: IAccountSummary): void {
    this.hasAccountSummaryContent = ![
      accountSummary.inspectionOrdersCompleted,
      accountSummary.inspectionSupplierVisited,
      accountSummary.inspectionAvgCost,
      accountSummary.inspectionBeyondAQLRate,
      accountSummary.inspectionServiceLevel,
      accountSummary.inspectionRejectionRate,
    ].every((item: number): boolean => item === 0);
  }

  public ngOnInit(): void {
    this._accountSummaryService
      .getAccountSummaryData$()
      .pipe(untilDestroyed(this))
      // eslint-disable-next-line rxjs/no-ignored-subscription
      .subscribe((accountSummary: QimaOptionalType<IAccountSummary>): void => {
        if (accountSummary) {
          this.accountSummary = accountSummary;
          this.getAccountSummaryStatus(accountSummary);
          this.inspectionOrdersCompleted = new AccountSummaryData(accountSummary.inspectionOrdersCompleted);
          this.inspectionSupplierVisited = new AccountSummaryData(accountSummary.inspectionSupplierVisited);
          this.inspectionAvgCost = new AccountSummaryCurrencyData(accountSummary.inspectionAvgCost);

          this.inspectionBeyondAQLRate = new AccountSummaryPercentageData(
            accountSummary.inspectionBeyondAQLRate,
            this.locale,
            ACCOUNT_SUMMARY.threshold30
          );

          this.inspectionServiceLevel = new AccountSummaryPercentageData(
            accountSummary.inspectionServiceLevel,

            this.locale,
            ACCOUNT_SUMMARY.threshold30
          );

          this.inspectionRejectionRate = new AccountSummaryPercentageData(
            accountSummary.inspectionRejectionRate,
            this.locale,
            ACCOUNT_SUMMARY.threshold50
          );

          this._changeDetectorRef.detectChanges();
        }
      });
  }
}
