import { EStorageKeys } from '@aca-new/app/shared/models/enums/storage-keys.enum';
import { AuthenticationService } from '@aca-new/app/shared/services/authentication.service';
import { Injectable } from '@angular/core';
import { CanActivate } from '@angular/router';
import { CookieService } from 'ngx-cookie';

@Injectable({ providedIn: 'root' })
export class AuthoritiesGuard implements CanActivate {
  public constructor(private readonly _cookieStorage: CookieService, private readonly _authenticationService: AuthenticationService) {}

  public canActivate(): boolean | Promise<boolean> {
    const userId = sessionStorage.getItem(EStorageKeys.USER_ID);
    const token = this._cookieStorage.get(EStorageKeys.AUTH_TOKEN);

    if (userId && token) {
      return true;
    }

    this._authenticationService.navigateToLogin();

    return false;
  }
}
