import {
  UPCOMING_ORDERS_TABLE_HEADERS,
  UPCOMING_ORDERS_TABLE_ID,
} from '@aca-new/app/pages/landing/pages/upcoming-orders/shared/constants/upcoming-orders-table.constants';
import { IDataTableSelect } from '@aca-new/app/pages/landing/shared/models/interfaces/data-table-select.interface';
import { IUser } from '@aca-new/app/pages/user/shared/models/interfaces/user.interface';
import { ITableHeader } from '@aca-new/app/shared/components/table/shared/interfaces/table-header.interface';
import { SERVER_API_URL } from '@aca-new/app/shared/constants/app.constants';
import { IMAGES } from '@aca-new/app/shared/constants/images.constants';
import { EStorageKeys } from '@aca-new/app/shared/models/enums/storage-keys.enum';
import { AuthenticationService } from '@aca-new/app/shared/services/authentication.service';
import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-upcoming-orders',
  templateUrl: './upcoming-orders.component.html',
  styleUrls: ['./upcoming-orders.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class UpcomingOrdersComponent implements OnInit {
  public readonly upcomingIcon: string = IMAGES.upcomingIcon;
  public readonly userData: IUser = this._authenticationService.getCurrentUser();
  public readonly userId = sessionStorage.getItem(EStorageKeys.USER_ID);
  public shouldUpcomingTable = false;
  public upcomingApiUrl: string = '';
  public upcomingSelectActive: string = '';
  public upcomingTableHeaders: ITableHeader[] = [];

  public readonly upcomingTableSelects: IDataTableSelect[] = [
    {
      label: 'UPCOMING_ORDERS.SELECT.INSPECTIONS',
      value: UPCOMING_ORDERS_TABLE_ID.inspectionsUpcoming,
    },
    {
      label: 'UPCOMING_ORDERS.SELECT.LAB_TESTING',
      value: UPCOMING_ORDERS_TABLE_ID.labTestingUpcoming,
    },
    {
      label: 'UPCOMING_ORDERS.SELECT.CERTIFICATIONS',
      value: UPCOMING_ORDERS_TABLE_ID.certificationUpcoming,
    },
    {
      label: 'UPCOMING_ORDERS.SELECT.AUDITS',
      value: UPCOMING_ORDERS_TABLE_ID.auditsUpcoming,
    },
  ];

  public readonly upcomingApiUrls: Record<string, string> = {
    [UPCOMING_ORDERS_TABLE_ID.auditsUpcoming]: `${SERVER_API_URL}/user/${this.userId}/audit-orders?status=${encodeURIComponent(
      '15,17,20,22,23,25,30,40,50'
    )}&page-size=5&page=1`,
    [UPCOMING_ORDERS_TABLE_ID.inspectionsUpcoming]: `${SERVER_API_URL}/user/${this.userId}/psi-orders?status=${encodeURIComponent(
      '15,17,20,22,23,25,30,40,50'
    )}&page-size=5&page=1`,
    [UPCOMING_ORDERS_TABLE_ID.labTestingUpcoming]: `${SERVER_API_URL}/user/${this.userId}/lt/orders?orderStatus=${encodeURIComponent(
      '20,21,22,23,24,25'
    )}&pageNumber=1&pageSize=5`,
    [UPCOMING_ORDERS_TABLE_ID.certificationUpcoming]: `${SERVER_API_URL}/user/${
      this.userId
    }/certification-orders?status=${encodeURIComponent('10,15,17,20,21,22,23,25,30,40,50,56')}&pageNumber=1&pageSize=5`,
  };

  public constructor(private readonly _authenticationService: AuthenticationService) {}

  public ngOnInit(): void {
    this.shouldUpcomingTable = !this.userData?.features?.viewReportsOnly;
    // init
    this.upcomingSelectActive = UPCOMING_ORDERS_TABLE_ID.inspectionsUpcoming;
    this.upcomingTableHeaders = UPCOMING_ORDERS_TABLE_HEADERS.inspections_upcoming;
    this.upcomingApiUrl = this.upcomingApiUrls.inspections_upcoming;
  }

  public upcomingSelectChange(id: Readonly<string>): void {
    this.upcomingApiUrl = this.upcomingApiUrls[id];
    this.upcomingTableHeaders = UPCOMING_ORDERS_TABLE_HEADERS[id];
  }
}
