import { IDataTableSelect } from '@aca-new/app/pages/landing/shared/models/interfaces/data-table-select.interface';
import { IDataTableData } from '@aca-new/app/shared/components/data-table/shared/models/interfaces/data-table-data.interface';
import { DataTableService } from '@aca-new/app/shared/components/data-table/shared/service/data-table.service';
import { ETableDataType } from '@aca-new/app/shared/components/table/shared/enums/table-data-type.enum';
import { ITableData } from '@aca-new/app/shared/components/table/shared/interfaces/table-data.interface';
import { ITableHeader } from '@aca-new/app/shared/components/table/shared/interfaces/table-header.interface';
import { ITableSort } from '@aca-new/app/shared/components/table/shared/interfaces/table-sort.interface';
import {
  Component,
  ChangeDetectionStrategy,
  Input,
  OnInit,
  Output,
  EventEmitter,
  ChangeDetectorRef,
  OnChanges,
  SimpleChanges,
} from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { EQimaDividerOrientation, EQimaIconName } from '@qima/ngx-qima';
import { QimaOptionalType } from '@qima/ngx-qima/models/types/qima-optional.type';
import { has } from 'lodash/index';

@UntilDestroy()
@Component({
  selector: 'app-data-table',
  templateUrl: './data-table.component.html',
  styleUrls: ['./data-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [DataTableService],
})
export class DataTableComponent implements OnInit, OnChanges {
  /**
   * @description
   * Set the config of the table selection in the landing page
   * @type {IDataTableSelect[]}
   * @default []
   */
  @Input('dataTableSelects')
  public selects: IDataTableSelect[] = [];

  /**
   * @description
   * Set the table title in the header
   * @type {string}
   * @default ''
   */
  @Input('dataTableTitle')
  public title: string = '';

  /**
   * @description
   * Set the icon of the landing table
   * @type {string}
   * @default ''
   */
  @Input('dataTableIcon')
  public icon: string = '';

  /**
   * @description
   * Set the fallback message of the empty table
   * @type {string}
   * @default ''
   */
  @Input('dataTableEmptyMessage')
  public emptyMessage: string = '';

  /**
   * @description
   * Show or hide the app-section-title component
   * @type {boolean}
   * @default true
   */
  @Input('dataTableIsShowSectionTitle')
  public isShowSectionTitle: boolean = true;

  /**
   * @description
   * Show or hide the table Pagination
   * @type {boolean}
   * @default false
   */
  @Input('dataTableShowPagination')
  public isShowPagination: boolean = false;

  /**
   * @description
   * The list of entries count per page
   * @type {number[]}
   * @default []
   */
  @Input('tablePageSizeList')
  public pageSizeList: number[] = [];

  /**
   * @description
   * Api for request data
   * @type {string}
   * @default ''
   */
  @Input('dataTableApiUrl')
  public apiUrl: QimaOptionalType<string> = '';

  @Input('dataTableHeaders')
  public tableHeaders: ITableHeader[] = [];

  @Input('dataTableIsShowMore')
  public isShowMore: boolean = false;

  @Input('dataTableSelectActive')
  public selectActive: string = '';

  @Input('dataTableSourceDataConvertTableData')
  // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
  public sourceDataConvertTableData: QimaOptionalType<(source: Readonly<IDataTableData>, headers: ITableHeader[]) => ITableData[][]>;

  // TODO:this should be handled in business component
  @Output('dataTableToolbarSelectChange')
  public readonly toolbarSelectChange: EventEmitter<Readonly<string>> = new EventEmitter<Readonly<string>>();

  @Output('dataTablePaginationChange')
  public readonly paginationChange: EventEmitter<Readonly<number>> = new EventEmitter<Readonly<number>>();

  @Output('dataTablePageSizeChange')
  public readonly pageSizeChange: EventEmitter<Readonly<number>> = new EventEmitter<Readonly<number>>();

  public tableData: ITableData[][] = [];
  public horizontal: EQimaDividerOrientation = EQimaDividerOrientation.HORIZONTAL;
  public chevronIconName: EQimaIconName = EQimaIconName.CHEVRON_DOWN;
  public tableTotalSize: number = 0;

  public constructor(private readonly _dataTableService: DataTableService, private readonly _changeDetectorRef: ChangeDetectorRef) {}

  public trackByIndex(index: Readonly<number>): number {
    return index;
  }

  public tableSort(sortEvent: Readonly<ITableSort>): void {
    // TODO sort
    console.log(sortEvent);
    this._dataTableService.updateSort('sort test');
  }

  public onToolbarSelectChange(selectId: Readonly<string>): void {
    this.toolbarSelectChange.emit(selectId);
  }

  // TODO
  public onFilter(): void {
    this._dataTableService.updateFilter('filter test');
  }

  public ngOnInit(): void {
    const { dataTableSubject$ } = this._dataTableService;

    // eslint-disable-next-line rxjs/no-ignored-subscription
    dataTableSubject$.pipe(untilDestroyed(this)).subscribe((res): void => {
      this.tableData = res;
      this._changeDetectorRef.markForCheck();
    });

    this._getTableData();
  }

  public ngOnChanges(changes: Readonly<SimpleChanges>): void {
    this._onApiUrlChange(changes);
  }

  public tablePaginationActivePageChange(page: Readonly<number>): void {
    this.paginationChange.emit(page);
  }

  public tablePaginationPageSizeChange(page: Readonly<number>): void {
    this.pageSizeChange.emit(page);
  }

  private _onApiUrlChange(changes: Readonly<SimpleChanges>): void {
    if (has(changes, 'apiUrl') && changes.apiUrl.currentValue !== changes.apiUrl.previousValue) {
      this._getTableData();
    }
  }

  private _getTableData(): void {
    if (!this.apiUrl) {
      return;
    }

    this._dataTableService
      .getTableData$(this.apiUrl)
      .pipe(untilDestroyed(this))
      // eslint-disable-next-line rxjs/no-ignored-subscription
      .subscribe((res: QimaOptionalType<IDataTableData>): void => {
        let tableData: ITableData[][] = [];

        // 1 convert to T[][]
        if (res) {
          // the response may like this
          // { totalSiza: 100, pageItems: {...}, ...}
          // or
          // { content: { totalSiza: 100, pageItems: {...},  ... } }
          res = res.content ? res.content : res;
          this.tableTotalSize = res.totalSize || 0;

          if (this.sourceDataConvertTableData) {
            tableData = this.sourceDataConvertTableData(res, this.tableHeaders);
          } else {
            tableData = this._sourceDataConvertTableData(res, this.tableHeaders);
          }
        }

        // 2 trigger next
        this._dataTableService.updateTableData(tableData);
      });
  }

  // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
  private _sourceDataConvertTableData(source: Readonly<IDataTableData>, headers: ITableHeader[]): ITableData[][] {
    const tableData: ITableData[][] = [];

    if (source.pageItems.length) {
      source.pageItems.forEach((pageItem): void => {
        const rowData: ITableData[] = [];

        headers?.forEach((head: ITableHeader): void => {
          const cellData: ITableData = {
            type: ETableDataType.STRING,
          };

          cellData.type = head.type;

          switch (cellData.type) {
            case ETableDataType.STRING:
              cellData.label = pageItem[head.key] as string;
          }

          rowData.push(cellData);
        });

        tableData.push(rowData);
      });
    }

    return tableData;
  }
}
