import { LandingComponent } from './landing.component';
import { AccountSummaryComponent } from '@aca-new/app/pages/landing/pages/account-summary/account-summary.component';
import { AccountSummaryCardComponent } from '@aca-new/app/pages/landing/pages/account-summary/shared/components/account-summary-card/account-summary-card.component';
import { BannerComponent } from '@aca-new/app/pages/landing/pages/banner/banner.component';
import { GetAppComponent } from '@aca-new/app/pages/landing/pages/banner/shared/component/get-app/get-app.component';
import { PhoneValidComponent } from '@aca-new/app/pages/landing/pages/banner/shared/component/phone-valid/phone-valid.component';
import { LatestReportsComponent } from '@aca-new/app/pages/landing/pages/latest-reports/latest-reports.component';
import { UpcomingOrdersComponent } from '@aca-new/app/pages/landing/pages/upcoming-orders/upcoming-orders.component';
import { DataTableModule } from '@aca-new/app/shared/components/data-table/data-table.module';
import { AuthoritiesGuard } from '@aca-new/app/shared/guards/authorities/authorities.guard';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import {
  QimaDividerModule,
  QimaInputSelectModule,
  QimaMenuModule,
  QimaMenuItemTextModule,
  QimaIconModule,
  QimaButtonModule,
  QimaButtonIconModule,
  QimaInputPhoneModule,
} from '@qima/ngx-qima';

const ROUTES: Routes = [
  {
    path: '',
    component: LandingComponent,
    canActivate: [AuthoritiesGuard],
  },
];

@NgModule({
  declarations: [
    LandingComponent,
    AccountSummaryComponent,
    AccountSummaryCardComponent,
    UpcomingOrdersComponent,
    LatestReportsComponent,
    BannerComponent,
    GetAppComponent,
    PhoneValidComponent,
  ],
  imports: [
    RouterModule.forChild(ROUTES),
    TranslateModule,
    QimaDividerModule,
    QimaInputSelectModule,
    QimaMenuItemTextModule,
    FormsModule,
    QimaMenuModule,
    QimaIconModule,
    CommonModule,
    QimaButtonModule,
    DataTableModule,
    QimaButtonModule,
    QimaButtonIconModule,
    QimaInputPhoneModule,
    ReactiveFormsModule,
  ],
  exports: [LandingComponent, AccountSummaryComponent, AccountSummaryCardComponent, UpcomingOrdersComponent, LatestReportsComponent],
})
export class LandingModule {}
