import {
  OPEN_BOOKINGS_TABLE_HEADERS,
  OPEN_BOOKINGS_TABLE_ID,
} from '@aca-new/app/pages/bookings/pages/open-bookings/shared/constants/open-booking-table.constants';
import { IDataTableSelect } from '@aca-new/app/pages/landing/shared/models/interfaces/data-table-select.interface';
import { ITableHeader } from '@aca-new/app/shared/components/table/shared/interfaces/table-header.interface';
import { SERVER_API_URL } from '@aca-new/app/shared/constants/app.constants';
import { TABLE_PAGINATION } from '@aca-new/app/shared/constants/table-pagination.constants';
import { EStorageKeys } from '@aca-new/app/shared/models/enums/storage-keys.enum';
import { ICommonTableUrl } from '@aca-new/app/shared/models/interfaces/common-table-url.interface';
import { ChangeDetectionStrategy, Component, OnInit, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'app-open-bookings',
  templateUrl: './open-bookings.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class OpenBookingsComponent implements OnInit {
  public readonly userId = sessionStorage.getItem(EStorageKeys.USER_ID);
  public openBookingsApiUrl: string = '';
  public openBookingsSelectActive: string = '';
  public openBookingsTableHeaders: ITableHeader[] = [];
  public openBookingsPageSizeList: number[] = [
    TABLE_PAGINATION.pageSize50,
    TABLE_PAGINATION.pageSize100,
    TABLE_PAGINATION.pageSize150,
    TABLE_PAGINATION.pageSize200,
    TABLE_PAGINATION.pageSize250,
  ];

  public readonly openBookingsTableSelects: IDataTableSelect[] = [
    {
      label: 'UPCOMING_ORDERS.SELECT.INSPECTIONS',
      value: OPEN_BOOKINGS_TABLE_ID.inspections,
    },
  ];

  public readonly openBookingsApiUrls: Record<string, ICommonTableUrl> = {
    [OPEN_BOOKINGS_TABLE_ID.inspections]: {
      url: `${SERVER_API_URL}/user/${this.userId}/psi-orders?status=${encodeURIComponent('15,17,18,20,22,23,25,30,40,50')}&`,
      params: 'page-size=50&page=1&sortType=desc&sortField=bookingDate',
    },
  };

  public ngOnInit(): void {
    this.openBookingsSelectActive = OPEN_BOOKINGS_TABLE_ID.inspections;
    this.openBookingsTableHeaders = OPEN_BOOKINGS_TABLE_HEADERS.inspections;
    this.openBookingsApiUrl =
      this.openBookingsApiUrls[OPEN_BOOKINGS_TABLE_ID.inspections].url +
      this.openBookingsApiUrls[OPEN_BOOKINGS_TABLE_ID.inspections].params;
  }

  public openBookingsSelectChange(id: Readonly<string>): void {
    this.openBookingsSelectActive = id;
    this.openBookingsApiUrl = this.openBookingsApiUrls[id].url + this.openBookingsApiUrls[id].params;
    this.openBookingsTableHeaders = OPEN_BOOKINGS_TABLE_HEADERS[id];
  }

  public openBookingsPaginationChange(page: Readonly<number>): void {
    const currentApiUrls = this.openBookingsApiUrls[this.openBookingsSelectActive];
    const searchParams = new URLSearchParams(currentApiUrls.params);

    searchParams.set('page', page.toString());
    this.openBookingsApiUrl = currentApiUrls.url + searchParams.toString();
  }
}
