import { EPhoneValidStatus } from '@aca-new/app/pages/landing/pages/banner/shared/models/enums/phone-valid-status.constants';
import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { EQimaIconName } from '@qima/ngx-qima';

@Component({
  selector: 'app-phone-valid',
  templateUrl: './phone-valid.component.html',
  styleUrls: ['./phone-valid.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PhoneValidComponent {
  /**
   * @description
   * The error or success content
   * type {string}
   * @default ''
   */
  @Input('phoneValidContent')
  public content: string = '';

  /**
   * @description
   * The error or success status
   * type {EPhoneValidStatus}
   * @default EPhoneValidStatus.NORMAL
   */
  @Input('phoneValidStatus')
  public status: EPhoneValidStatus = EPhoneValidStatus.NORMAL;

  public readonly phoneIconName: EQimaIconName = EQimaIconName.ALERT;
}
