import { IBanner } from '@aca-new/app/pages/landing/pages/banner/shared/models/interfaces/banner.interface';
import { MARKETING_WEBSITE_APP_LINK } from '@aca-new/app/shared/constants/app.constants';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable()
export class BannerService {
  public constructor(private readonly _httpClient: HttpClient) {}

  public sendAppLink$(data: IBanner): Observable<HttpResponse<string>> {
    return this._httpClient.post(
      `${MARKETING_WEBSITE_APP_LINK}/smsMobileLink`,
      { countryCode: data.countryCode, phoneNumber: data.phoneNumber, token: 'p7qWgaBTxlCaPQpz9wGI' },
      { observe: 'response', responseType: 'text' }
    );
  }
}
