import { IDataTableData } from '@aca-new/app/shared/components/data-table/shared/models/interfaces/data-table-data.interface';
import { ITableData } from '@aca-new/app/shared/components/table/shared/interfaces/table-data.interface';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { UntilDestroy } from '@ngneat/until-destroy';
import { QimaOptionalType } from '@qima/ngx-qima';
import { BehaviorSubject, combineLatest, Observable, Subject } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable()
@UntilDestroy()
export class DataTableService {
  public readonly tableData$: Subject<ITableData[][]> = new BehaviorSubject<ITableData[][]>([]);
  // TODO handle head cell sort
  public readonly sort$: BehaviorSubject<string> = new BehaviorSubject<string>('');
  // TODO handle filter
  public readonly filter$: BehaviorSubject<string> = new BehaviorSubject<string>('');

  public dataTableSubject$ = combineLatest([this.tableData$, this.sort$, this.filter$]).pipe(
    map(([tableData, sort, filter]): ITableData[][] => {
      // TODO handle table data sort & filter and so on
      console.log(tableData);
      console.log(sort);
      console.log(filter);

      return tableData;
    })
  );

  public constructor(private readonly _httpClient: HttpClient) {}

  // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
  public updateTableData(tableData: ITableData[][]): void {
    this.tableData$.next(tableData);
  }

  public updateSort(str: string): void {
    this.sort$.next(str);
  }

  public updateFilter(str: string): void {
    this.filter$.next(str);
  }

  public getTableData$(url: string): Observable<QimaOptionalType<IDataTableData>> {
    return this._httpClient.get<IDataTableData>(url, { observe: 'response' }).pipe(
      map(
        // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
        (response: Readonly<HttpResponse<IDataTableData>>): QimaOptionalType<IDataTableData> => response?.body
      )
    );
  }
}
