import { ITableData } from '@aca-new/app/shared/components/table/shared/interfaces/table-data.interface';
import { ITableHeader } from '@aca-new/app/shared/components/table/shared/interfaces/table-header.interface';
import { ITableSort } from '@aca-new/app/shared/components/table/shared/interfaces/table-sort.interface';
import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { UntilDestroy } from '@ngneat/until-destroy';
import { QimaOptionalType } from '@qima/ngx-qima/models/types/qima-optional.type';

@UntilDestroy()
@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss'],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class TableComponent {
  /**
   * @description
   * Set the data of the table header
   * @type {QimaOptionalType<ITableHeader[]>}
   * @default []
   */
  @Input('tableHeaders')
  public tableHeaders: QimaOptionalType<ITableHeader[]> = [];

  /**
   * @description
   * Set the fallback message of the empty table
   * @type {string}
   * @default ''
   */
  @Input('tableEmptyMessage')
  public emptyMessage: string = '';

  /**
   * @description
   * Set the data of the table
   * @type {ITableData[][]}
   * @default []
   */
  @Input('tableData')
  public tableData: ITableData[][] = [];

  /**
   * @description
   * output event when click table sort
   */
  @Output() public readonly tableSort: EventEmitter<ITableSort> = new EventEmitter<ITableSort>();

  public trackByIndex(index: Readonly<number>): number {
    return index;
  }

  public qimaTableHeadCellSortAscendingChange(containsSortEvent: Readonly<boolean>, index: Readonly<number>): void {
    this.tableSort.emit({ sortingMode: containsSortEvent, position: index });
  }
}
