import { IAccountSummary } from '@aca-new/app/pages/landing/pages/account-summary/shared/service/models/interfaces/account-summary.interface';
import { SERVER_API_URL } from '@aca-new/app/shared/constants/app.constants';
import { EStorageKeys } from '@aca-new/app/shared/models/enums/storage-keys.enum';
import { IHttpResponseBody } from '@aca-new/app/shared/models/interfaces/http-response-body.interface';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable()
export class AccountSummaryService {
  public constructor(private readonly _httpClient: HttpClient) {}

  public getDateRange(type?: string): string {
    let date;

    if (type === 'today') {
      date = new Date();
    } else {
      // 1  full year before now
      date = new Date(new Date().setFullYear(new Date().getFullYear() - 1));
    }

    return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
  }

  public getAccountSummaryData$(): Observable<IAccountSummary | null> {
    const userId = sessionStorage.getItem(EStorageKeys.USER_ID);

    return this._httpClient
      .get<IHttpResponseBody<IAccountSummary>>(
        `${SERVER_API_URL}/user/${userId}/dashboard-overview?startDate=${this.getDateRange()}&endDate=${this.getDateRange('today')}`,
        { observe: 'response' }
      )
      .pipe(
        map(
          // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
          (response: Readonly<HttpResponse<IHttpResponseBody<IAccountSummary>>>): IAccountSummary | null => response?.body?.content || null
        )
      );
  }
}
