import { IAsideItem } from '@aca-new/app/shared/components/aside/shared/models/interfaces/aside-item.interface';
import { AsideService } from '@aca-new/app/shared/components/aside/shared/services/aside.service';
import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { TranslateService } from '@ngx-translate/core';
import { EQimaIconName, QimaOptionalType } from '@qima/ngx-qima';

@UntilDestroy()
@Component({
  selector: 'app-aside',
  templateUrl: './aside.component.html',
  styleUrls: ['./aside.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [AsideService],
})
export class AsideComponent implements OnInit {
  /**
   * @description
   * Show or hide the aside menu of the app
   * @type {boolean}
   * @default false
   */
  public isAsideMenuShow: boolean = false;
  public asideItemCheckedIndex: QimaOptionalType<number> = undefined;

  public menuItems: IAsideItem[] = [
    {
      label: this._translateService.instant('APP_ASIDE.COMMON_ITEMS.ANALYTICS'),
      iconName: EQimaIconName.ANALYTICS,
    },
    {
      label: this._translateService.instant('APP_ASIDE.COMMON_ITEMS.BOOKINGS'),
      iconName: EQimaIconName.ASSIGNMENTS,
      path: '/bookings/open',
    },
    {
      label: this._translateService.instant('APP_ASIDE.COMMON_ITEMS.REPORTS'),
      iconName: EQimaIconName.REPORTS,
      path: '/reports',
    },
    {
      label: this._translateService.instant('APP_ASIDE.COMMON_ITEMS.CHECKLISTS'),
      iconName: EQimaIconName.CHECKLIST,
    },
    {
      label: this._translateService.instant('APP_ASIDE.COMMON_ITEMS.SUPPLIERS'),
      iconName: EQimaIconName.SUPPLIER_FACTORY,
    },
    {
      label: this._translateService.instant('APP_ASIDE.COMMON_ITEMS.PAYMENTS'),
      iconName: EQimaIconName.PAYMENT,
      path: '/payments/unpaid',
    },
  ];

  public constructor(
    private readonly _translateService: TranslateService,
    private readonly _asideService: AsideService,
    private readonly _router: Router
  ) {}

  public ngOnInit(): void {
    const { asideMenuShowSubject$ } = this._asideService;

    // eslint-disable-next-line rxjs/no-ignored-subscription
    asideMenuShowSubject$.pipe(untilDestroyed(this)).subscribe((isShow: boolean): void => {
      this.isAsideMenuShow = isShow;
    });
  }

  public trackByIndex(index: Readonly<number>): number {
    return index;
  }

  public onAsideItemClick(index: Readonly<number>, item: IAsideItem): void {
    this.asideItemCheckedIndex = index;
    this._asideService.updateAsideMenuShow(false);

    if (item.path) {
      void this._router.navigate([item.path]);
    }
  }

  public onOverlayClick(): void {
    this._asideService.updateAsideMenuShow(false);
  }

  public onBookNowClick(): void {
    this._asideService.updateAsideMenuShow(!this.isAsideMenuShow);
  }
}
