import { IUser } from '@aca-new/app/pages/user/shared/models/interfaces/user.interface';
import { EStorageKeys } from '@aca-new/app/shared/models/enums/storage-keys.enum';
import { AuthenticationService } from '@aca-new/app/shared/services/authentication.service';
import { ChangeDetectionStrategy, Component } from '@angular/core';
import { CookieService } from 'ngx-cookie';

@Component({
  selector: 'app-banner',
  templateUrl: './banner.component.html',
  styleUrls: ['./banner.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BannerComponent {
  public readonly currentUser: IUser = this._authenticationService.getCurrentUser();
  public isShowGetApp = this._getBannerStatus();
  public constructor(private readonly _cookieStorage: CookieService, private readonly _authenticationService: AuthenticationService) {}

  public closeClick(): void {
    this.isShowGetApp = this._getBannerStatus();
  }

  private _getBannerStatus(): boolean {
    return !this._cookieStorage.get(EStorageKeys.SEND_MOBILE_APP_LINK);
  }
}
