import { ICommonTableTab } from '@aca-new/app/shared/models/interfaces/common-table-tab.interface';
import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-bookings',
  templateUrl: './bookings.component.html',
  styleUrls: ['./bookings.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BookingsComponent implements OnInit {
  public tabs: ICommonTableTab[] = [
    {
      label: 'BOOKINGS.OPEN_BOOKINGS',
      isSelect: true,
      routerLink: '/bookings/open',
    },
    {
      label: 'BOOKINGS.COMPLETED_BOOKINGS',
      isSelect: false,
      routerLink: '/bookings/completed',
    },
    {
      label: 'BOOKINGS.DRAFTS',
      isSelect: false,
      routerLink: '/bookings/drafts',
    },
  ];

  public constructor(private readonly _router: Router) {}

  public trackByIndex(index: Readonly<number>): number {
    return index;
  }

  public ngOnInit(): void {
    // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
    this.tabs.map((item: ICommonTableTab): void => {
      item.isSelect = item.routerLink === this._router.url;
    });
  }
}
