import { EPhoneValidStatus } from '@aca-new/app/pages/landing/pages/banner/shared/models/enums/phone-valid-status.constants';
import { BannerService } from '@aca-new/app/pages/landing/pages/banner/shared/service/banner.service';
import { IMAGES } from '@aca-new/app/shared/constants/images.constants';
import { EStorageKeys } from '@aca-new/app/shared/models/enums/storage-keys.enum';
import { HttpResponse } from '@angular/common/http';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, EventEmitter, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { FormControl } from '@ngneat/reactive-forms';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { TranslateService } from '@ngx-translate/core';
import {
  EQimaButtonIconStyle,
  EQimaButtonSize,
  EQimaButtonWidth,
  EQimaIconName,
  EQimaInputSize,
  EQimaIsoCountryCode,
  QIMA_COUNTRY_AND_AREA_ITEMS,
} from '@qima/ngx-qima';
import { IQimaCountry } from '@qima/ngx-qima/models/interfaces/qima-country.interface';
import { QimaOptionalType } from '@qima/ngx-qima/models/types/qima-optional.type';
import { QimaStringBooleanType } from '@qima/ngx-qima/models/types/qima-string-boolean.type';
import { CookieService } from 'ngx-cookie';

@UntilDestroy()
@Component({
  selector: 'app-get-mobile-application',
  templateUrl: './get-app.component.html',
  styleUrls: ['./get-app.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  providers: [BannerService],
})
export class GetAppComponent implements OnInit {
  @Output('closeClick')
  public readonly closeClick: EventEmitter<MouseEvent | Event> = new EventEmitter<MouseEvent | Event>();

  public readonly closeButtonIconStyle: EQimaButtonIconStyle = EQimaButtonIconStyle.TERTIARY;
  public readonly closeIconName: EQimaIconName = EQimaIconName.CLOSE;
  public readonly closeButtonSize: EQimaButtonSize = EQimaButtonSize.SMALL;
  public readonly sendLinkButtonWidth: EQimaButtonWidth = EQimaButtonWidth.FULL;
  public readonly sendLinkButtonSize: EQimaButtonSize = EQimaButtonSize.LARGE;
  public readonly sendLinkButtonLabel: string = 'APP_LANDING.BANNER.SEND_LINK';
  public readonly inputPhoneSize: EQimaInputSize = EQimaInputSize.LARGE;
  public readonly bannerLogo: string = IMAGES.bannerLogo;
  public phoneValidContent: string = '';
  public isLinkButtonDisabled: boolean = true;
  public phoneValidStatus: EPhoneValidStatus = EPhoneValidStatus.NORMAL;
  public isShowPhoneValid: boolean = false;

  public defaultCountry: QimaOptionalType<IQimaCountry> = undefined;
  public inputPhoneHasValidators: QimaOptionalType<QimaStringBooleanType> = true;
  public formControl: FormControl<string> = new FormControl('');

  public constructor(
    private readonly _cookieStorage: CookieService,
    private readonly _bannerService: BannerService,
    private readonly _changeDetectorRef: ChangeDetectorRef,
    private readonly _translateService: TranslateService
  ) {}

  public ngOnInit(): void {
    this.defaultCountry = QIMA_COUNTRY_AND_AREA_ITEMS.find((country: IQimaCountry): boolean => country.iso2 === EQimaIsoCountryCode.CHINA);
    this.formControl.markAsDirty();
    this.formControl.markAsTouched();

    // eslint-disable-next-line rxjs/no-ignored-subscription
    this.formControl.valueChanges.pipe(untilDestroyed(this)).subscribe((data: string): void => {
      this.isLinkButtonDisabled = this.getRealPhone(data).length <= 0;
    });
  }

  public dealWithBannerCookie(): void {
    this._cookieStorage.put(EStorageKeys.SEND_MOBILE_APP_LINK, 'true');
    this.closeClick.emit();
  }

  public onCloseClick(): void {
    this.dealWithBannerCookie();
  }

  public getRealPhone(value: string): string {
    if (value) {
      return value.split(')')[1].replace(/ /g, '');
    }

    return '';
  }

  public onSendClick(): void {
    if (this.formControl.value?.includes(')')) {
      const phoneNumber: string = this.getRealPhone(this.formControl.value);

      this.phoneValidStatus = EPhoneValidStatus.NORMAL;
      this.phoneValidContent = this._translateService.instant('APP_LANDING.BANNER.CHECKING');
      this.isShowPhoneValid = true;

      this._bannerService
        .sendAppLink$({ countryCode: this.defaultCountry?.dialCodes[0], phoneNumber })
        .pipe(untilDestroyed(this))
        // eslint-disable-next-line rxjs/no-ignored-subscription,@typescript-eslint/prefer-readonly-parameter-types
        .subscribe((response: Readonly<HttpResponse<string>>): void => {
          if (response.body === 'Sending') {
            this.phoneValidContent = this._translateService.instant('APP_LANDING.BANNER.LINK_SENT', { mobileNumber: phoneNumber });
            this.dealWithBannerCookie();

            return;
          }

          this.phoneValidStatus = EPhoneValidStatus.DANGER;
          this.phoneValidContent = response.body || '';

          this._changeDetectorRef.markForCheck();
        });
    }
  }

  public onInputPhoneClear(): void {
    this.isShowPhoneValid = false;
    this.phoneValidContent = '';
    this.phoneValidStatus = EPhoneValidStatus.NORMAL;
    this.isLinkButtonDisabled = true;
  }
}
