import { ChangeDetectionStrategy, Component, Input } from '@angular/core';
import { QimaOptionalType } from '@qima/ngx-qima';

@Component({
  selector: 'app-account-summary-card',
  templateUrl: './account-summary-card.component.html',
  styleUrls: ['./account-summary-card.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccountSummaryCardComponent {
  /**
   * @description
   * The content of the account summary
   * type {string}
   * @default ''
   */
  @Input('accountSummaryContent')
  public content: QimaOptionalType<string> = '';

  /**
   * @description
   * The status of the account summary
   * type {string}
   * @default ''
   */
  @Input('accountSummaryStatus')
  public status: QimaOptionalType<string> = '';

  /**
   * @description
   * The description of the account summary
   * type {string}
   * @default ''
   */
  @Input('accountSummaryDescription')
  public description: string = '';
}
