import { ICompanyLogo } from '@aca-new/app/shared/components/header/shared/interfaces/companyLogo.interface';
import { SERVER_API_URL } from '@aca-new/app/shared/constants/app.constants';
import { IHttpResponseBody } from '@aca-new/app/shared/models/interfaces/http-response-body.interface';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class HeaderService {
  public constructor(private readonly _httpClient: HttpClient) {}

  public getCompanyLogo$(companyId: string): Observable<string | null> {
    const userId = sessionStorage.getItem('userId');

    return (
      this._httpClient
        .get<ICompanyLogo>(`${SERVER_API_URL}/user/${userId}/company/${companyId}/logo`, { observe: 'response' })
        // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
        .pipe(map((response: Readonly<HttpResponse<ICompanyLogo>>): string | null => response?.body?.image || null))
    );
  }

  public getSicImage$(sicId: string): Observable<string | null> {
    return this._httpClient
      .get<IHttpResponseBody<string>>(`${SERVER_API_URL}/parameter/sic/${sicId}/base64?refresh=true`, { observe: 'response' })
      .pipe(
        // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
        map((response: Readonly<HttpResponse<IHttpResponseBody<string>>>): string | null => response?.body?.content || null)
      );
  }
}
