import {
  LATEST_REPORTS_TABLE_HEADERS,
  LATEST_REPORTS_TABLE_ID,
} from '@aca-new/app/pages/landing/pages/latest-reports/shared/constants/latest-reports-table.constants';
import { IDataTableSelect } from '@aca-new/app/pages/landing/shared/models/interfaces/data-table-select.interface';
import { IDataTableData, PageItemType } from '@aca-new/app/shared/components/data-table/shared/models/interfaces/data-table-data.interface';
import { ETableDataType } from '@aca-new/app/shared/components/table/shared/enums/table-data-type.enum';
import { ITableData } from '@aca-new/app/shared/components/table/shared/interfaces/table-data.interface';
import { ITableHeader } from '@aca-new/app/shared/components/table/shared/interfaces/table-header.interface';
import { SERVER_API_URL } from '@aca-new/app/shared/constants/app.constants';
import { IMAGES } from '@aca-new/app/shared/constants/images.constants';
import { EStorageKeys } from '@aca-new/app/shared/models/enums/storage-keys.enum';
import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { QimaOptionalType } from '@qima/ngx-qima/models/types/qima-optional.type';

@Component({
  selector: 'app-latest-reports',
  templateUrl: './latest-reports.component.html',
  styleUrls: ['./latest-reports.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LatestReportsComponent implements OnInit {
  public readonly reportIcon: string = IMAGES.reportIcon;
  public userId = sessionStorage.getItem(EStorageKeys.USER_ID);
  public shouldReportTable = false;
  public reportApiUrl: string = '';
  public lastReportsSelectActive: string = '';
  public reportTableHeaders: ITableHeader[] = [];
  // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
  public sourceDataConvertTableData: QimaOptionalType<(source: Readonly<IDataTableData>, headers: ITableHeader[]) => ITableData[][]> =
    undefined;

  public readonly reportTableSelects: IDataTableSelect[] = [
    {
      label: 'LATEST_REPORTS.SELECT.INSPECTIONS',
      value: LATEST_REPORTS_TABLE_ID.inspectionsReport,
    },
    {
      label: 'LATEST_REPORTS.SELECT.LAB_TESTING',
      value: LATEST_REPORTS_TABLE_ID.labTestingReport,
    },
    {
      label: 'LATEST_REPORTS.SELECT.AUDITS',
      value: LATEST_REPORTS_TABLE_ID.auditsReport,
    },
  ];

  public readonly reportApiUrls: Record<string, string> = {
    [LATEST_REPORTS_TABLE_ID.inspectionsReport]: `${SERVER_API_URL}/user/${this.userId}/reports?page-size=5&page=1&sortType=desc&sortField=inspectionDate`,
    [LATEST_REPORTS_TABLE_ID.labTestingReport]: `${SERVER_API_URL}/user/${
      this.userId
    }/lt/reports?pageSize=5&pageNumber=1&sampleArrivedDateFrom=${this.getDateRange('today')}&sampleArrivedDateTo=${this.getDateRange(
      'next-year'
    )}`,
    [LATEST_REPORTS_TABLE_ID.auditsReport]: `${SERVER_API_URL}/user/${this.userId}/audit-reports?page-size=5&page=1&sortType=desc&sortField=AUDIT_DATE`,
  };

  public ngOnInit(): void {
    // TODO handle the access show report table
    // const userData = JSON.parse(sessionStorage.getItem(EStorageKeys.USER_DATA) || '{}');
    // this.shouldReportTable = userData?.company?.extraAccess?.canSeeReportsPage;
    this.shouldReportTable = true;
    // init
    this.lastReportsSelectActive = LATEST_REPORTS_TABLE_ID.inspectionsReport;
    this.reportApiUrl = this.reportApiUrls.inspections_report;
    this.reportTableHeaders = LATEST_REPORTS_TABLE_HEADERS.inspections_report;
  }

  public getDateRange(type: string): string {
    let date;

    if (type === 'today') {
      date = new Date();
    } else {
      // 1 full year from now
      date = new Date(new Date().setFullYear(new Date().getFullYear() + 1));
    }

    return `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`;
  }

  public reportSelectChange(id: Readonly<string>): void {
    this.reportApiUrl = this.reportApiUrls[id];
    this.reportTableHeaders = LATEST_REPORTS_TABLE_HEADERS[id];

    if (id === LATEST_REPORTS_TABLE_ID.labTestingReport) {
      this.sourceDataConvertTableData = this.sourceDataConvertTableDataForLabTesting;
    } else {
      this.sourceDataConvertTableData = undefined;
    }
  }

  // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
  public sourceDataConvertTableDataForLabTesting(source: Readonly<IDataTableData>, headers: Readonly<ITableHeader[]>): ITableData[][] {
    const tableData: ITableData[][] = [];

    if (source.pageItems.length) {
      source.pageItems.forEach((pageItem): void => {
        const rowData: ITableData[] = [];

        headers?.forEach((head: ITableHeader): void => {
          const cellData: ITableData = {
            type: ETableDataType.STRING,
          };

          cellData.type = head.type;

          switch (cellData.type) {
            case ETableDataType.STRING:
              cellData.label = (pageItem.order as Record<string, PageItemType>)[head.key] as string;
          }

          rowData.push(cellData);
        });

        tableData.push(rowData);
      });
    }

    return tableData;
  }
}
