import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output, ViewEncapsulation } from '@angular/core';
import { EQimaIceCubeSize, EQimaIceCubeStyle } from '@qima/ngx-qima';

@Component({
  selector: 'app-previous-order-item',
  templateUrl: './previous-order-item.component.html',
  styleUrls: ['./previous-order-item.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
})
export class PreviousOrderItemComponent {
  /**
   * @description
   * The label of ice cube of the item
   * @type {string}
   * @default ''
   */
  @Input('previousOrderItemIceCubeLabel')
  public iceCubeLabel = '';

  /**
   * @description
   * The name of the previous order
   * @type {string}
   * @default ''
   */
  @Input('previousOrderItemOrderName')
  public orderName = '';

  /**
   * @description
   * The date of the previous order
   * @type {string}
   * @default ''
   */
  @Input('previousOrderItemOrderDate')
  public orderDate = '';

  @Output('previousOrderItemClick')
  public readonly itemClick: EventEmitter<MouseEvent | Event> = new EventEmitter<MouseEvent | Event>();

  public iceCubeSize: EQimaIceCubeSize = EQimaIceCubeSize.SMALL;

  public iceCubeStyle: EQimaIceCubeStyle = EQimaIceCubeStyle.CHERRY;

  public isIceCubeHasOutline: boolean = false;

  public onClick(event: Readonly<MouseEvent | Event>): void {
    this._emitClick(event);
  }

  public onEnter(event: Readonly<Event>): void {
    this._emitClick(event);
  }

  private _emitClick(event: Readonly<MouseEvent | Event>): void {
    this.itemClick.emit(event);
  }
}
