import { ACCOUNT_SUMMARY } from '@aca-new/app/pages/landing/pages/account-summary/shared/constants/account-summary.constants';
import { EAccountSummaryStatus } from '@aca-new/app/pages/landing/pages/account-summary/shared/models/enums/account-summary-status.constants';
import { formatPercent } from '@angular/common';

export class AccountSummaryData {
  public content: string = '';
  public status: EAccountSummaryStatus = EAccountSummaryStatus.DEFAULT;

  public constructor(value: number, threshold?: number) {
    this.content = value.toString();

    if (threshold) {
      this.status = value <= threshold ? EAccountSummaryStatus.DANGER : EAccountSummaryStatus.NORMAL;
    }
  }
}

export class AccountSummaryPercentageData extends AccountSummaryData {
  public constructor(value: number, local: string, threshold?: number) {
    super(value, threshold);
    this.content = formatPercent(value, local);
  }
}

export class AccountSummaryCurrencyData extends AccountSummaryData {
  public constructor(value: number, threshold?: number) {
    super(value, threshold);
    // eslint-disable-next-line no-magic-numbers
    this.content = `${ACCOUNT_SUMMARY.prefixUsd} ${value.toFixed(3)}`;
  }
}
