import { IUser } from '@aca-new/app/pages/user/shared/models/interfaces/user.interface';
import { UserService } from '@aca-new/app/pages/user/shared/service/user.service';
import { EStorageKeys } from '@aca-new/app/shared/models/enums/storage-keys.enum';
import { AuthenticationService } from '@aca-new/app/shared/services/authentication.service';
import { ChangeDetectionStrategy, Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { CookieService } from 'ngx-cookie';

@UntilDestroy()
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [UserService],
})
export class LoginComponent implements OnInit {
  public userId: string = '';
  public token: string = '';

  public constructor(
    private readonly _userService: UserService,
    private readonly _authenticationService: AuthenticationService,
    private readonly _activatedRoute: ActivatedRoute,
    private readonly _cookieStorage: CookieService,
    private readonly _router: Router
  ) {}

  public ngOnInit(): void {
    this._getAuthInfoByRoute();
    this._getUserData();
  }

  private _getAuthInfoByRoute(): void {
    // eslint-disable-next-line rxjs/no-ignored-subscription
    this._activatedRoute.queryParams.pipe(untilDestroyed(this)).subscribe((params): void => {
      const { token, userId } = params;

      if (token && userId) {
        this.userId = userId;
        this.token = token;
        this._cookieStorage.put(EStorageKeys.AUTH_TOKEN, token);
        sessionStorage.setItem(EStorageKeys.USER_ID, userId);
      }
    });
  }

  private _getUserData(): void {
    if (!this.userId) {
      return;
    }

    this._userService
      .getUserById$(this.userId)
      .pipe(untilDestroyed(this))
      // eslint-disable-next-line rxjs/no-ignored-subscription
      .subscribe((user: IUser | null): void => {
        if (user) {
          sessionStorage.setItem(EStorageKeys.USER_DATA, JSON.stringify(user));
          this._authenticationService.updateUserData(user);
          void this._router.navigate(['/landing']);
        }
      });
  }
}
