import { CHEVRON_RIGHT_SIZE } from '@aca-new/app/shared/components/aside/shared/constants/aside-style.constants';
import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { EQimaIconName } from '@qima/ngx-qima';

@Component({
  selector: 'app-book-now',
  templateUrl: './book-now.component.html',
  styleUrls: ['./book-now.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class BookNowComponent {
  /**
   * @description
   * Check or not
   * @type {boolean}
   * @default false
   */
  @Input('isBookNowChecked')
  public isChecked: boolean = false;

  public inspectIcon: EQimaIconName = EQimaIconName.INSPECT;
  public chevronRightIcon: EQimaIconName = EQimaIconName.CHEVRON_RIGHT;
  public chevronRightSize: number = CHEVRON_RIGHT_SIZE;

  @Output('bookNowClick')
  public readonly bookNowClick: EventEmitter<MouseEvent | Event> = new EventEmitter<MouseEvent | Event>();

  public onClick(event: Readonly<MouseEvent>): void {
    this._emitClick(event);
  }

  public onEnter(event: Readonly<Event>): void {
    this._emitClick(event);
  }

  private _emitClick(event: Readonly<MouseEvent | Event>): void {
    this.bookNowClick.emit(event);
  }
}
