import { IUser } from '@aca-new/app/pages/user/shared/models/interfaces/user.interface';
import { HeaderService } from '@aca-new/app/shared/components/header/shared/service/header.service';
import { IMAGES } from '@aca-new/app/shared/constants/images.constants';
import { AuthenticationService } from '@aca-new/app/shared/services/authentication.service';
import { Component, ChangeDetectionStrategy, OnInit, ChangeDetectorRef } from '@angular/core';
import { UntilDestroy, untilDestroyed } from '@ngneat/until-destroy';
import { TranslateService } from '@ngx-translate/core';
import { EQimaAvatarSize, EQimaDividerOrientation, EQimaIconName } from '@qima/ngx-qima';

@UntilDestroy()
@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class HeaderComponent implements OnInit {
  // TODO need to add interface to currentUser
  public currentUser: IUser = this._authenticationService.getCurrentUser();
  public parentCompanyName: string = '';
  public logoutIconName: EQimaIconName = EQimaIconName.LOGOUT;
  public infoIconName: EQimaIconName = EQimaIconName.INFORMATION;
  public chevronIconName: EQimaIconName = EQimaIconName.CHEVRON_DOWN;
  public vertical: EQimaDividerOrientation = EQimaDividerOrientation.VERTICAL;
  public sicImage: string = '';
  public companyLogo: string = '';
  public qimaLogo: string = IMAGES.qimaLogo;
  public isDropDownVisible: boolean = false;
  public avatarFirstName: string = '';
  public avatarSize: EQimaAvatarSize = EQimaAvatarSize.MIN;
  public avatarLastName: string = '';

  public dropDownList = [
    {
      title: this._translateService.instant('APP_NAV_TOP.ACCOUNT.YOUR_ACCOUNT'),
    },
    {
      title: this._translateService.instant('APP_NAV_TOP.ACCOUNT.PREFERENCES'),
    },
    {
      title: this._translateService.instant('APP_NAV_TOP.ACCOUNT.YOUR_DOCUMENTS'),
    },
  ];

  public constructor(
    private readonly _translateService: TranslateService,
    private readonly _headerService: HeaderService,
    private readonly _changeDetectorRef: ChangeDetectorRef,
    private readonly _authenticationService: AuthenticationService
  ) {}

  public openProfileActions(): void {
    this.isDropDownVisible = !this.isDropDownVisible;
  }

  public trackByIndex(index: Readonly<number>): number {
    return index;
  }

  public logout(): void {
    this._authenticationService.logout();
  }

  public formatAvatarName(): void {
    if (this.currentUser?.sic) {
      const sicArray = this.currentUser?.sic.split(' ');

      [this.avatarFirstName] = sicArray;

      if (sicArray.length > 1) {
        this.avatarLastName = sicArray[sicArray.length - 1];
      }
    }
  }

  public getUserInfo(): void {
    if (this._authenticationService.isAuthenticated()) {
      this._getCompanyLogo();
      this._getSic();
      this.formatAvatarName();
      this.parentCompanyName = this.currentUser.company?.parentCompanyName;
    }
  }

  public ngOnInit(): void {
    this.getUserInfo();
    const { currentUserSubject$ } = this._authenticationService;

    // eslint-disable-next-line rxjs/no-ignored-subscription,@typescript-eslint/prefer-readonly-parameter-types
    currentUserSubject$.pipe(untilDestroyed(this)).subscribe((data: Readonly<IUser>): void => {
      this.currentUser = data;
      this.getUserInfo();
    });
  }

  private _getCompanyLogo(): void {
    if (this.currentUser?.company?.id) {
      this._headerService
        .getCompanyLogo$(this.currentUser.company.id)
        .pipe(untilDestroyed(this))
        // eslint-disable-next-line rxjs/no-ignored-subscription
        .subscribe((companyLogo: string | null): void => {
          if (companyLogo) {
            this.companyLogo = companyLogo;
            this._changeDetectorRef.markForCheck();
          }
        });
    }
  }

  private _getSic(): void {
    if (this.currentUser?.company?.id) {
      this._headerService
        .getSicImage$(this.currentUser?.sicId)
        .pipe(untilDestroyed(this))
        // eslint-disable-next-line rxjs/no-ignored-subscription
        .subscribe((sicImage: string | null): void => {
          if (sicImage) {
            this.sicImage = sicImage;
          }
        });
    }
  }
}
