import { AppRoutingModule } from './app-routing.module';
import { LandingModule } from './pages/landing/landing.module';
import { LoginModule } from './pages/user/pages/login/login.module';
import { ELocales } from './shared/models/enums/locales.enum';
import { BookingsModule } from '@aca-new/app/pages/bookings/bookings.module';
import { MainComponent } from '@aca-new/app/shared/components/main/main.component';
import { MainModule } from '@aca-new/app/shared/components/main/main.module';
import { HttpLoaderFactory, LocalInitProvider } from '@aca-new/app/shared/factories/i18n.factories';
import { AuthInterceptor } from '@aca-new/app/shared/interceptors/auth.interceptor';
import { ErrorHandlerInterceptor } from '@aca-new/app/shared/interceptors/error-handler.interceptor';
import { HTTP_INTERCEPTORS, HttpBackend, HttpClientModule } from '@angular/common/http';
import { APP_INITIALIZER, FactoryProvider, LOCALE_ID, NgModule, ValueProvider } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { TranslateLoader, TranslateModule, TranslateService } from '@ngx-translate/core';
import {
  EQimaLoggerLevel,
  EQimaTranslationKey,
  QIMA_ASSETS_PATH_TOKEN,
  QIMA_DATE_FACTORY_TOKEN,
  QIMA_TRANSLATION_FACTORY_TOKEN,
  QimaDateFactory,
  QimaDateModule,
  QimaDateType,
  QimaLoggerModule,
  QimaModule,
  QimaTranslationFactory,
  QimaTranslationModule,
} from '@qima/ngx-qima';
import { QimaOptionalType } from '@qima/ngx-qima/models/types/qima-optional.type';
import { AngularSvgIconModule } from 'angular-svg-icon';
import dayjs from 'dayjs';
import localizedFormat from 'dayjs/plugin/localizedFormat';
import timezone from 'dayjs/plugin/timezone';
import 'dayjs/locale/en';
import 'dayjs/locale/es';
import 'dayjs/locale/zh-cn';
import { CookieModule } from 'ngx-cookie';
import { BehaviorSubject, Observable } from 'rxjs';

dayjs.extend(localizedFormat);
dayjs.extend(timezone);

class TranslationService implements QimaTranslationFactory {
  public readonly activeLanguage$: BehaviorSubject<string> = new BehaviorSubject<string>(this._localeId);

  public constructor(private readonly _translateService: TranslateService, private readonly _localeId: string) {}

  public translate$(translationKey: Readonly<EQimaTranslationKey>, extra?: Readonly<Record<string, unknown>>): Observable<string> {
    // Note the prefix to match the JSON paths
    return this._translateService.get(`ngx-qima.${translationKey}`, extra);
  }
}

/**
 * @param {TranslateService} translateService from ngx-translate/core
 * @param {string} localeId local id
 * @returns {TranslationService} translation service
 */
// eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
function TranslationFactory(translateService: TranslateService, localeId: string): TranslationService {
  return new TranslationService(translateService, localeId);
}

/**
 * @returns {DateService} date service
 */
function DateFactory(): DateService {
  return new DateService();
}

class DateService implements QimaDateFactory {
  public format(date: Readonly<QimaOptionalType<QimaDateType>>, format: Readonly<string>): string {
    return dayjs(date).format(format);
  }
}

@NgModule({
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    QimaModule.forRoot(),
    AppRoutingModule,
    QimaDateModule,
    LoginModule,
    LandingModule,
    BookingsModule,
    HttpClientModule,
    QimaTranslationModule.forRoot(),
    TranslateModule.forRoot({
      defaultLanguage: ELocales.ENGLISH,
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpBackend],
      },
    }),
    // Used by ngx-qima library
    AngularSvgIconModule.forRoot(),
    QimaTranslationModule.forRoot(),
    CookieModule.forRoot(),
    MainModule,
    QimaLoggerModule.forRoot({
      loggerLevel: EQimaLoggerLevel.DEBUG,
    }),
  ],
  providers: [
    {
      provide: QIMA_DATE_FACTORY_TOKEN,
      useFactory: DateFactory,
    } as FactoryProvider,
    {
      provide: APP_INITIALIZER,
      useFactory: LocalInitProvider,
      deps: [TranslateService],
      multi: true,
    },
    {
      provide: QIMA_ASSETS_PATH_TOKEN,
      useValue: 'ngx-qima/assets/',
    } as ValueProvider,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true,
    },
    {
      provide: HTTP_INTERCEPTORS,
      useClass: ErrorHandlerInterceptor,
      multi: true,
    },
    {
      provide: QIMA_TRANSLATION_FACTORY_TOKEN,
      useFactory: TranslationFactory,
      deps: [TranslateService, LOCALE_ID],
    } as FactoryProvider,
  ],
  bootstrap: [MainComponent],
})
export class AppModule {}
