import { IUser } from '@aca-new/app/pages/user/shared/models/interfaces/user.interface';
import { MARKETING_WEBSITE } from '@aca-new/app/shared/constants/app.constants';
import { EStorageKeys } from '@aca-new/app/shared/models/enums/storage-keys.enum';
import { ENVIRONMENT } from '@aca-new/environments/environment';
import { DOCUMENT } from '@angular/common';
import { Inject, Injectable } from '@angular/core';
import { CookieService } from 'ngx-cookie';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class AuthenticationService {
  public readonly currentUserSubject$: Subject<IUser> = new Subject<IUser>();

  public constructor(private readonly _cookieStorage: CookieService, @Inject(DOCUMENT) private readonly _document: Document) {}

  // eslint-disable-next-line @typescript-eslint/prefer-readonly-parameter-types
  public updateUserData(data: Readonly<IUser>): void {
    this.currentUserSubject$.next(data);
  }

  public logout(): void {
    sessionStorage.removeItem(EStorageKeys.USER_DATA);
    sessionStorage.removeItem(EStorageKeys.USER_ID);
    this._cookieStorage.remove(EStorageKeys.AUTH_TOKEN);
    this._document.location.href = `${MARKETING_WEBSITE}/logout`;
  }

  public navigateToLogin(): void {
    location.assign(ENVIRONMENT.NEW_ACA_LOGIN_URL);
  }

  public isAuthenticated(): boolean {
    return sessionStorage.getItem(EStorageKeys.USER_DATA) !== null;
  }

  public getCurrentUser(): IUser {
    return JSON.parse(sessionStorage.getItem(EStorageKeys.USER_DATA) || '{}');
  }
}
